.CubeIncludes{
    padding: 40px 40px;
    border-radius:5px;
    max-width:800px;
    background: #FDF7EA;
    border: 3px solid #D5D5D3;
    box-shadow: 12px 16px 0px rgba(0, 0, 0, 0.06);
    border-radius: 25px;
}



.CubeIncludes .btn-primary{
  margin-top: 40px;
  max-width: 600px;
  margin-top: 40px;
  padding: 10px 20px;
    font-size: 1.5rem;
}

.CubeIncludes h2{
    font-size: 2.5rem !important;
    padding-bottom: 50px;
    text-shadow: 0px 8px 0px rgba(0, 0, 0, 0.25);
    font-family: 'Montserrat', sans-serif;
  }



.CubeIncludes svg{
    color: #A76A25;
    font-size: 36px;
}



@media only screen and (max-width: 600px) {

    .ItemIncluded img{
      width: 30px !important;
      position: relative;
      left: 0px;
      top: 10px;
      float: right;
    }

}